import React from 'react'
import {Link} from 'react-router-dom'

 const Nav = () => {
    return (
        <div>
            <nav class="navbar navbar-transparent navbar-color-on-scroll fixed-top navbar-expand-lg" color-on-scroll="100" id="sectionsNav">
    <div class="container">
      <div class="navbar-translate">
        <Link class="navbar-brand" href="https://demos.creative-tim.com/material-kit/index.html">
          Material Kit </Link>
        <button class="navbar-toggler" type="button" data-toggle="collapse" aria-expanded="false" aria-label="Toggle navigation">
          <span class="sr-only">Toggle navigation</span>
          <span class="navbar-toggler-icon"></span>
          <span class="navbar-toggler-icon"></span>
          <span class="navbar-toggler-icon"></span>
        </button>
      </div>
      <div class="collapse navbar-collapse">
        <ul class="navbar-nav ml-auto">
          
          <li class="nav-item">
            <Link class="nav-link" to="/" >
               Home
            </Link>
          </li>
          <li class="nav-item">
            <Link class="nav-link" to="/Contact" >
               Contact
            </Link>
          </li>
          <li class="nav-item">
            <Link class="nav-link" rel="tooltip" title="" data-placement="bottom" href="https://twitter.com/CreativeTim" target="_blank" data-original-title="Follow us on Twitter" rel="nofollow">
              <i class="fa fa-twitter"></i>
            </Link>
          </li>
          <li class="nav-item">
            <Link class="nav-link" rel="tooltip" title="" data-placement="bottom" href="https://www.facebook.com/CreativeTim" target="_blank" data-original-title="Like us on Facebook" rel="nofollow">
              <i class="fa fa-facebook-square"></i>
            </Link>
          </li>
          <li class="nav-item">
            <Link class="nav-link" rel="tooltip" title="" data-placement="bottom" href="https://www.instagram.com/CreativeTimOfficial" target="_blank" data-original-title="Follow us on Instagram" rel="nofollow">
              <i class="fa fa-instagram"></i>
            </Link>
          </li>
        </ul>
      </div>
    </div>
  </nav>
        </div>
    )
}
export default Nav